* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-size: 1.7vw;
  color: #333;
}
i.iconfont {
  font-size: 3vw;
  margin-right: 1vw;
}
a {
  text-decoration: none;
  color: inherit;
}

.resume-container {
  position: relative;
  margin: 5vw auto;
  width: 90vw;
  padding: 10vmin 3vmin;
  background: #fff;
  box-shadow: 0 0 0.5vw 0 rgba(0, 0, 0, 0.5);
  border-radius: 3vw;
  z-index: auto;
}
.resume-container::after {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  border-radius: 3vw;
  box-shadow: 0 0 0.5vw 0 rgba(0, 0, 0, 0.3);
  background: linear-gradient(to right, #22d3ee, #0ea5e9);
  z-index: -1;
  transform: rotate(-1deg);
}
.resume-personal-profile {
  position: relative;
  height: 26vw;
}
.resume-personal-profile-name > h2 {
  color: #000;
  font-size: 5vw;
  letter-spacing: 0.5vw;
}
.resume-personal-profile-photo {
  position: absolute;
  top: 0;
  right: 0;
  width: 20vw;
  height: 20vw;
}
.resume-personal-profile-photo > img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 50%;
}

.resume-personal-profile-basic {
  display: grid;
  width: 65vw;
  height: 15vw;
  grid-template-columns: repeat(2, 1fr);
  align-items: center;
}
.resume-personal-profile-basic > div {
  display: flex;
}
.resume-personal-profile-basic > div > span {
  line-height: 3vw;
}
.resume-personal-profile-basic > div  a {
  line-height: 3vw;
  color: #0ea5e9;
}
.resume-title {
  font-size: 3vw;
  font-weight: bold;
  height: 8vw;
  line-height: 8vw;
  border-bottom: 0.2vw solid #ccc;
}
.resume-content {
  border-left: 0.2vw solid #0ea5e9;
  padding: 2vw 2vw;
  margin: 3vw 0;
}
.resume-container div.resume-time {
  font-size: 2vw;
  color: #909090;
  margin-right: 2vw;
  font-weight: normal;
}
.icon-dian {
  position: absolute;
  left: -2.08vw;
  top: 50%;
  color: #0ea5e9;
  transform: translate(-50%, -50%);
}
.resume-edu-experience-item {
  margin-top: 1vw;
  position: relative;
  display: flex;
  align-items: center;
}
.resume-edu-experience-item > div {
  font-size: 2vw;
  margin-right: 2vw;
  font-weight: bold;
}
.resume-work-experience-top {
  margin-top: 1vw;
  position: relative;
  display: flex;
  align-items: center;
}
.resume-work-experience-top > div {
  font-size: 2vw;
}
.resume-work-experience-top > .resume-work-experience-job {
  margin-left: 2vw;
  font-size: 1.8vw;
  color: #909090;
}
.resume-work-experience-department {
  font-size: 2vw;
  margin: 1vw 0;
}
.resume-work-experience-jobDescription {
  font-size: 1.8vw;
}
.resume-professional-skills-container {
  margin: 3vw 0;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  row-gap: 2vw;
  justify-items: center;
}
.resume-professional-skills-item {
  height: 20vw;
  width: 20vw;
  border-radius: 50%;
  background: #d5f7fd;
  text-align: center;
  font-size: 1.5vw;
  font-weight: bold;
  padding: 7vw 1vw 0 1vw;
  box-shadow: 0 0 1.5vw 0.1vw #0ea5e9;
}
.resume-project-experience-container {
  margin: 3vw 0;
}
.resume-project-experience-name {
  height: 5vw;
  font-size: 2.4vw;
  font-weight: bold;
  line-height: 5vw;
}
.resume-project-experience-detail {
  display: grid;
  grid-template-columns: 1fr 7fr;
  margin-bottom: 8vw;
}
.resume-project-experience-detail-item {
  font-size: 2vw;
}
.resume-project-experience-detail-item p {
  font-size: 2vw;
}
.resume-project-experience-detail-item p > strong {
  margin: 0 0.5vw;
  font-size: 2vw;
}
.resume-project-experience-detail-item:nth-child(2n + 1) {
  color: #909090;
}
.resume-self-evaluation-content {
  margin: 3vw 0;
  font-size: 2vw;
  line-height: 2;
}
